@import (less) "color.less";


.switch-binary-disabled {
  #switchBinaryLabel {
    cursor: not-allowed;
    pointer-events: none !important;
    opacity: .5;
  }
  #switchBinarySliderBox {
    cursor: not-allowed;
    pointer-events: none !important;
  }
 }

.switch-binary {
    position: relative;
    display: inline-block;
    width: 100px;
    height: 50px;
    cursor: pointer;
    background-color: @light_gray;
    -webkit-transition: 0s;
    transition: 0s;

    .switch-label-icon-on {
        font-weight: bold;
        text-align: center;
        font-size: 0.750em;
        color: @white;
        width: 50px;
        display: table-cell;
        line-height: 50px;
        box-shadow: inset 0.250em 0.250em 0.250em 0 rgba(0, 0, 0, 0.2);

    }

    .switch-label-icon-off {
        font-weight: bold;
        text-align: center;
        font-size: 0.750em;
        color: @gray;
        width: 50px;
        display: table-cell;
        line-height: 50px;
        box-shadow: inset 0.250em 0.250em 0.250em 0 rgba(0, 0, 0, 0.2);
    }
}

.slider-box {
  position: absolute;
  content: "";
  cursor: pointer;
  height: 50px;
  width: 50px;
  left: 0px;
  bottom: 0px;
  background-color: white;
  -webkit-transition: 0s;
  transition: 0s;
  border: 1px solid @light_gray
}

.switch-binary-true {
  background-color: @green;
}

.switch-binary-true-disabled {
  background-color: @lighter_green;
}

.switch-binary-true .slider-box {
  -webkit-transform: translateX(50px);
  -ms-transform: translateX(50px);
  transform: translateX(50px);
}

:host-context(.contrasto-on) {

    .slider-box {
        background-color: @white;
        border-color: @black;
    }

    .switch-binary {
        .switch-label-icon-on {
            color: @white;
        }
        .switch-label-icon-off {
            color: @white;
        }
    }

    .switch-binary-true {
        background-color: @black;
    }
}